@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  width: 100vw;
  height: 50px;
}

a {
  color: black;
  text-decoration: none;
}
a:visited {
  text-decoration: none;
  color: black;
}

.home {
  height: auto;
}

.projects {
  height: auto;
}

.contact {
  height: auto;
}

.outer {
  display: flex;
  justify-content: center;
  position: absolute;
}

.square {
  height: 300px;
  width: 300px;
  border: 1px solid black;
  margin-top: 10vh;
  margin-left: 8vh;
  margin-left: 8vh;
}

.image {
  height: 300px;
  width: 300px;
}

.main-display {
  background: rgba(0, 0, 0, 0.8);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.displayed-project {
  background-color: white;
  width: 650px;
  height: 650px;
  position: absolute;
}

iframe {
  width: 650px;
  height: 650px;
}

.Container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 10vh;
  flex-wrap: wrap;
}

.main-container {
  display: flex;
  flex-direction: column;
}

.picofme {
  width: auto;
  height: 400px;
  margin-left: 50px;
}

.container1 {
  display: flex;
  margin: 100px;
  height: 400px;
}
.Name {
  font-size: 100px;
  margin-top: 0;
  font-weight: 900;
}

.text1 {
  font-family: "Spartan";
  padding: 10px;
}

.container2 {
  display: flex;
  margin: 50px;
  height: 400px;
}

.fail {
  height: 300px;
  width: auto;
  margin-top: 50px;
  margin-right: 50px;
}

.text2 {
  font-family: "Spartan";
  padding: 130px;
  margin-left: 200px;
}

.container3 {
  display: flex;
  margin: 100px;
  height: 400px;
}

.text3 {
  font-family: "Spartan";
  padding: 100px;
  margin-right: 200px;
}

.gaming {
  width: auto;
  height: 300px;
  margin-left: 50px;
}

